/* stylelint-disable */
html {
  font-family: sans-serif;
  text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

progress {
  vertical-align: baseline; }

template,
[hidden] {
  display: none; }

a {
  background-color: transparent;
  text-decoration-skip: objects; }

a:active,
a:hover {
  outline-width: 0; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration-style: underline dotted; }

b,
strong {
  font-weight: inherit; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background-color: #ffff00;
  color: #000000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: 1em; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

button,
input,
select,
textarea {
  font: inherit;
  margin: 0; }

optgroup {
  font-weight: bold; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  appearance: none; }

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54; }

::-webkit-file-upload-button {
  font: inherit; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: "Montserrat", sans-serif;
  color: #212121;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  overflow-x: hidden; }

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px; }

.button {
  font-size: 18px;
  line-height: 22px;
  background-color: #ff5900;
  color: #ffffff;
  padding: 20px 40px;
  border-radius: 100px;
  cursor: pointer; }

.header__wrap {
  display: flex;
  flex-direction: column; }

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 104px;
  margin-bottom: 28px; }

.header__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%; }

.header__link {
  color: #212121;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  opacity: 0.7;
  margin-right: 40px; }

.header__button {
  background-color: #ffffff;
  color: #ff5900;
  padding: 16px 28px;
  border: 1px solid #ff5900;
  border-radius: 100px; }

.header__row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 28px; }

.header__search {
  padding: 0; }

.nav {
  width: 100%; }

.nav__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-between;
  margin: 0;
  padding: 0; }

.nav__dropdown {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%; }

.nav__link,
.nav__batton {
  font-size: 14px;
  line-height: 120%;
  text-transform: uppercase;
  color: #212121; }

.nav__link--dropdown {
  opacity: 0.5;
  display: inline-block;
  padding: 16px;
  width: 100%;
  border-bottom: 1px solid rgba(51, 51, 51, 0.1); }

.nav__batton {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer; }

.nav__item--dropdown {
  position: relative;
  border-bottom: 1px solid rgba(51, 51, 51, 0.1); }

.nav__dropdown {
  position: absolute;
  min-width: 352px;
  background-color: #ffffff;
  display: none; }

.nav__item--dropdown:hover .nav__dropdown {
  display: block; }

.search {
  margin-left: 40px;
  position: relative; }

.search__button {
  padding: 0;
  border: none;
  cursor: pointer; }
  .search__button::before {
    position: absolute;
    content: "";
    background-image: url("../img/svg-icon/search-icon.svg");
    width: 24px;
    height: 24px;
    top: 2px;
    left: -22px; }

.marketing {
  background-color: #333333;
  background-image: url("../img/background.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  min-height: 716px;
  margin-bottom: 120px; }
  @media (min-resolution: 2dppx) {
    .marketing {
      background-image: url("../img/background@2x.webp"); } }

.marketing__title {
  display: inline-block;
  margin-top: 170px;
  margin-bottom: 28px;
  width: 780px;
  font-weight: 700;
  font-size: 68px;
  line-height: 110%;
  color: #ffffff; }

.marketing__text {
  width: 780px;
  margin: 0;
  color: #ffffff;
  margin-bottom: 52px; }

.marketing__button {
  border: none; }
  .marketing__button--alternative {
    background-color: rgba(255, 255, 255, 0.2);
    margin-left: 32px; }

.packages {
  margin-bottom: 120px; }

.packages__title {
  font-weight: 700;
  font-size: 38px;
  line-height: 120%;
  color: #ff5900;
  margin: 0;
  margin-bottom: 60px; }

.packages__title span {
  color: #212121; }

.packages__wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 60px; }

.packages__card {
  min-height: 487px; }

.card-package {
  display: flex;
  flex-direction: column;
  padding: 40px 32px;
  color: #ffffff;
  background-image: url("../img/package-yandex.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%; }
  @media (min-resolution: 2dppx) {
    .card-package {
      background-image: url("../img/package-yandex@2x.webp"); } }
  .card-package--crisis {
    background-image: url("../img/package-classic.webp"); }
    @media (min-resolution: 2dppx) {
      .card-package--crisis {
        background-image: url("../img/package-classic@2x.webp"); } }
  .card-package--sicial {
    background-image: url("../img/package-soc.webp"); }
    @media (min-resolution: 2dppx) {
      .card-package--sicial {
        background-image: url("../img/package-soc@2x.webp"); } }

.card-package__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 140%;
  margin-bottom: 28px; }

.card-package__text {
  margin: 0;
  padding: 20px 0; }
  .card-package__text--top {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  .card-package__text--condition {
    font-size: 14px;
    line-height: 140%;
    opacity: 0.5; }

.card-package__button {
  margin-top: auto;
  background-color: transparent;
  border: 1px solid #ff5900;
  border-radius: 100px;
  color: #ff5900;
  text-align: center;
  font-size: 16px;
  line-height: 140%;
  padding: 16px; }

.features__title {
  font-weight: 700;
  font-size: 38px;
  line-height: 120%;
  margin: 0;
  margin-bottom: 60px; }

.features__wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 60px; }

.featur {
  padding: 45px 15px;
  text-align: center; }

.featur__icon {
  margin-bottom: 16px; }

.featur__text {
  margin: 0;
  margin-bottom: 10px; }
